import React, {useContext, useState} from 'react'
import {FloatButton, Select} from "antd";
import {SyncOutlined} from "@ant-design/icons";

import {BookContext} from "./Book";

export function FloatingMenu({count}) {
    let [expand, setExpand] = useState(false)
    let {bookState, dispatch} = useContext(BookContext)

    const getArray = length => {
        return Array(length).fill(1).map((item, index) => ({value: index + 1, label: index + 1}))
    }

    return <>
        {expand &&
            <>
                {/* ONE */}
                <FloatButton type={bookState.repeat.oneToggle ? "primary" : ""}
                             description={"1"}
                             style={{bottom: "7rem"}}
                             onClick={()=>dispatch({type: "one"})}/>
                {/* A-B */}
                <FloatButton type={bookState.repeat.manyToggle ? "primary" : ""}
                             description={"A-B"}
                             style={{bottom: "4rem"}}
                             onClick={()=>dispatch({type: "many"})}/>
                <FloatButton
                    shape={"square"}
                    description={<Select style={{width: "4rem"}} defaultValue={bookState.repeat.fromSloka} options={getArray(count)}
                                         onSelect={val => dispatch({type: "repeatFrom", data: val})}/>}
                    style={{width: "4rem", bottom: "4rem", right: "8rem"}}/>
                <FloatButton
                    shape={"square"}
                    description={<Select style={{width: "4rem"}} defaultValue={bookState.repeat.toSloka} options={getArray(count)}
                                         onSelect={val => dispatch({type: "repeatTo", data: val})}/>}
                    style={{width: "4rem", bottom: "4rem", right: "4rem"}}/>
            </>
        }
        <FloatButton icon={<SyncOutlined/>} style={{bottom: "1rem"}}
                     onClick={() => setExpand(!expand)}/>
    </>
}
