import {Card, Button} from "antd";
import {useContext, useEffect, useRef, useState} from "react";

import {BookContext} from "./Book";
import * as util from "../utils/Util";

export function Sloka(props) {
    let {
        chapNum, slokaNum,
        slokam, meaning,
        updateActiveSlokaFn, active,
        audioUrl,
        getSlokaFromCurrentTime
    } = props

    let {bookState, dispatch} = useContext(BookContext)
    const [collapseNum, setCollapseNum] = useState(null);
    const slokaRef = useRef({
        audio: null,    // audio file
        currentSloka: null,
        active: null,
        // for Repeat Object
        repeat: {
            oneToggle: false,
            manyToggle: false,
            fromSloka: 1,
            toSloka: 5
        }
    });

    // for last sloka pause
    useEffect(() => {
        util.fetcher.audioTime.then(data => {
            slokaRef.current.timeData = data.audio
        })
        dispatch({type: "TimeUpdateListener", data: monitor})
    }, [])

    useEffect(() => {
        if (!active) {
            return
        }

        slokaRef.current.active = active
        console.log(active, "C:Sloka useEffect[active]", active)
        // if not seen in screen, scroll..
        let activeElement = document.querySelector("#slokam" + active)
        if (activeElement) {
            if (!util.isInViewport(activeElement)) {
                let prevElement = document.querySelector("#slokam" + (active - 1))
                prevElement.scrollIntoView({behavior: "smooth"})
            }
        }
    }, [active])

    useEffect(() => {
        slokaRef.current.repeat = bookState.repeat
    }, [bookState.repeat]);

    //
    // REMEMBER: this is a LISTENER,
    //          state will not work, only useRef will work..
    //
    const monitor = () => {
        // this method CALLED, for approx 4 times in a second
        console.log("monitor-1: ", song.currentTime)
        // console.log("monitor-1: ", song.currentTime, slokaRef.current.repeat)

        // 1. updating Active sloka i.e NEXT SLOKA (while playing)
        let slokaNumFromTime = getSlokaFromCurrentTime(song.currentTime)
        let activeRef = slokaRef.current.active
        if (slokaNumFromTime !== activeRef) {
            console.log("monitor-2 (time, slokaFromTime, activeRef): ", song.currentTime, slokaNumFromTime, activeRef)

            // Repeat ONE
            if (bookState.repeat.oneToggle) {
                song.currentTime = util.toSeconds(slokaRef.current.timeData[chapNum][activeRef])
                return
            } else if (bookState.repeat.manyToggle) {
                // Repeat Many
                let {fromSloka, toSloka} = slokaRef.current.repeat
                if (slokaNumFromTime < fromSloka || slokaNumFromTime > toSloka) {
                    song.currentTime = util.toSeconds(slokaRef.current.timeData[chapNum][fromSloka])
                    updateActiveSlokaFn(fromSloka)
                } else if (slokaNumFromTime - 1 === toSloka) {
                    song.currentTime = util.toSeconds(slokaRef.current.timeData[chapNum][fromSloka])
                    updateActiveSlokaFn(fromSloka)
                } else {
                    updateActiveSlokaFn(slokaNumFromTime)
                }
                return
            }

            // goto NEXT sloka
            updateActiveSlokaFn(slokaNumFromTime)
        }

        // 2. PAUSE @ end of chapter
        if (activeRef === Object.entries(slokaRef.current.timeData[chapNum]).length) {
            let chapEndTime = util.toSeconds(slokaRef.current.timeData[chapNum][activeRef]) + 15 + 20; // ex: calculating EOC (end of chap)
            if (song.currentTime > chapEndTime) {       // stop playing @ end of chapter
                song.pause();
                console.log('--( Chapter end )--')
            }
        }
    }

    const toggleSlokaMeaning = e => slokaNumber => {
        e.stopPropagation()
        setCollapseNum(slokaNumber === collapseNum ? 0 : slokaNumber)
    }

    const playPause = slokaClicked => {
        console.log("C:Sloka.playPause(): chap, slokam, audio ", chapNum, slokaNum, audioUrl)

        if (!active || active !== slokaClicked) {
            updateActiveSlokaFn(slokaClicked)
        } else {
            song.paused ? song.play() : song.pause()
            return
        }

        // set sloka window & play
        document.querySelector("#songSource").src = audioUrl
        song.currentTime = util.toSeconds(audioUrl.split('#t=')[1])
        song.play()
    }

    return <Card
        id={"slokam" + slokaNum}
        size="small"
        className="bgEven"
        style={{border: 0, backgroundColor: "unset", fontSize: 20, margin: 0}}
        bodyStyle={{padding: 0}}>
        {/*--------- 1. Slokam ---------*/}
        <div className="talapatra cardcss" style={{padding: "1rem"}}
             onClick={() => playPause(slokaNum)}
        >
            <Button className="meaningButton"
                    style={{border: active === slokaNum ? "2px solid #906720" : 0, backgroundColor: "unset"}}
                    onClick={e => toggleSlokaMeaning(e)(slokaNum)}>
                {chapNum + "." + slokaNum}
            </Button>
            {slokam.split("\\n").map((line, key) =>
                <span key={key}>
                    {line} <br/>
                </span>
            )}
        </div>
        <div style={{display: collapseNum === slokaNum ? "block" : "none", padding: "0.6rem"}}>
            {/*--------- 2. Word Meanings ---------*/}
            <div className="slokaMeaning">
                {meaning && meaning.meaning}
            </div>
            {/*--------- 3. Slokam Meaning ---------*/}
            <div className="slokaTranslation">
                {meaning && meaning.translation}
            </div>
        </div>
    </Card>;
}