import React, {useState, useEffect, useReducer, createContext} from 'react';

import {Chapter} from './Chapter';
import * as util from "../utils/Util";
import {Audio} from "./Audio";

// ----------------------1. Reducer (state manager)----------------------
function bookReducer(state, action) {
    switch (action.type) {
        case "TimeUpdateListener":
            if (!state.timeupdateListener) {
                console.log("Reducer(case)TimeUpdateListener",)
                state.timeupdateListener = action.data
                song.addEventListener('timeupdate', state.timeupdateListener);
            }
            break
        case "one":
            state.repeat.oneToggle = !state.repeat.oneToggle
            state.repeat.manyToggle = false
            break
        case "many":
            state.repeat.manyToggle = !state.repeat.manyToggle
            state.repeat.oneToggle = false
            break
        case "repeatFrom":
            state.repeat.fromSloka = action.data
            break
        case "repeatTo":
            state.repeat.toSloka = action.data
            break
    }

    state = {...state}
    return state
}

let initialState = {
    // 1. audio tag
    timeupdateListener: null,
    // 2. repeat button group
    repeat: {
        oneToggle: false,
        manyToggle: false,
        fromSloka: 1,
        toSloka: 5
    }
}

// ----------------------2. Context (descendent children to access)----------------------
export let BookContext = createContext()

// ----------------------3. Component: Book----------------------
//
export default function Book({chapterNum}) {
    chapterNum = chapterNum || 1;

    //
    // Load whole book contents,
    //  i.e chapter names, slokas, meanings, times
    //
    const [bookContent, setBookContent] = useState(null);
    useEffect(() => {
        (async function f1() {
            let [
                slokasAndTitles,
                meaning,
                audioTimeline
            ] = await Promise.all(
                [
                    util.fetcher.slokas,
                    util.fetcher.meaning,
                    util.fetcher.audioTime
                ])

            setBookContent({
                title: slokasAndTitles.title,
                slokas: slokasAndTitles.slokas,
                meaning: meaning.slokasMeaning,
                audioTime: audioTimeline.audio
            });
        })();
    }, []);

    let [bookState, dispatch] = useReducer(bookReducer, initialState)

    const getSlokaFromCurrentTime = currTime => {
        let slokaTimeMap = Object.entries(bookContent.audioTime[chapterNum])

        for (let i = 1; i < slokaTimeMap.length; i++) {
            let [aIndex, aStartTime, bIndex, bStartTime] = [...slokaTimeMap[i - 1], ...slokaTimeMap[i]]
            let [a, b] = [util.toSeconds(aStartTime), util.toSeconds(bStartTime)]
            aIndex = parseInt(aIndex)
            bIndex = parseInt(bIndex)
            if (a <= currTime && currTime <= b) {
                return aIndex
            } else if (bIndex === slokaTimeMap.length) {
                return bIndex
            }
        }
    }


    return bookContent?.slokas &&
        <>
            <BookContext.Provider value={{bookState, dispatch}}>
                <Chapter chapterContent={{
                    number: chapterNum,
                    name: bookContent.title[chapterNum],
                    slokas: bookContent.slokas[chapterNum],
                    meaning: bookContent.meaning[chapterNum],
                    audioTime: bookContent.audioTime[chapterNum],
                    audioUrl: util.getAudioUrl(chapterNum),
                    getSlokaFromCurrentTime
                }}/>
                <Audio chapNum={chapterNum}/>
            </BookContext.Provider>
        </>
}
